    /* Pop Up*/
      /*Pop Up troféu */
        .popup {
          width: 55px;
          height: 55px;
          border-radius: 30px;
          border: 0.01px solid #6d486f;
          background-color: #EA8D8D;
          font-family: 'Chakra Petch';
          color: white;
          background-image: url(/static/media/trofeu.05b368a3.png);
          background-size: contain;
          display: inline-block;
          cursor: pointer;
          position: absolute;
          margin-top: 15px;
          margin-left: -92px;
        }
        /*Fundo pop up info */
          .popup .popuptext {
            visibility: hidden;
            background-color: #ea8d8d;
            text-align: left;
            height: -15px;
            width: 355px;
            margin-left: -410px;
            color: #fff;
            border-radius: 20px;
            position: absolute;
            z-index: 1;
            bottom: 125%;
            left: 600%;
            padding: 10px;
            line-height: 22px;
          }
        /* Popup arrow */
          .popup .popuptext::after {
            content: "";
            position: absolute;
            top: 100%;
            left: 50%;
            margin-left: -5px;
          } 
        /* Toggle this class when clicking on the popup container (hide and show the popup) */
          .popup .popuptext.show {
            visibility: visible;
            -webkit-animation: fadeIn 1s;
            animation: fadeIn 1s;
          }  
        /* Add animation (fade in the popup) */
          @-webkit-keyframes fadeIn {
            from {opacity: 0;}
            to {opacity: 1;}
          }
          @keyframes fadeIn {
            from {opacity: 0;}
            to {opacity:1 ;}
          }

