.main-wrapper {
  background-color: gray;
  
  width: calc(100vw - 2rem);
  height: calc(100vh - 2rem);
	padding: 1rem;
    
  display: grid;
  grid-template-rows: 8rem 1fr 10rem;
  grid-template-columns: 1fr 30rem;
  grid-row-gap: 1rem;
  grid-column-gap: 1rem;
  place-items: center center;
  
  grid-template-areas:
    "header aside"
    "game aside"
    "footer footer";
}


.header-jogo {
	grid-area: header;
	border-radius: 1rem;
	display: flex;
	width: 100%;
	height: 100%;
	background-color: #D18EB7;
  border: 2px solid black;


}

.main-jogo {
	grid-area: game;
	display: flex;
	border-radius: 1rem;
	width: 100%;
	height: 100%;
  border: 2px solid black;
	background-color: #3C183D;
}

.aside-jogo {
	grid-area: aside;
	display: flex;
	width: 100%;
	height: 100%;
  border: 2px solid black;
	background-color: #D18EB7;
	border-radius: 1rem;
}


.footer-jogo {
	grid-area: footer;
	border-radius: 1rem;
	display: flex;
	width: 100%;
	height: 100%;
  border: 2px solid black;
	background-color: #D18EB7;	
}
